<template>
  <div id="activity" ref="contentWrapper">
      <div class="content-wrapper" @touchstart="refreshScrollOnce">
        <div class="header">
          <div class="member">
            <mu-icon-button icon="arrow_back" href="#/org/example"/>
            <div class="name">
              新东方的主页
            </div>
            <mu-icon-button icon="more_vert"/>
          </div>
        </div>
        <span class="notice">
          <img src="../assets/images/icon-xdf.png">
          <mu-raised-button label="+ 订阅" labelClass="label" class="demo-raised-button" blue/>
        </span>
        <span class="topic">新东方夏季大促，立享90%off，现在报名最高免费。</span>
        <img src="../assets/images/off.png" class="off">
        <p class="text">
          考研无忧计划-标准版1.可最多选报国内考试部开设的45人以上考研走读班型24个；VIP一对一辅导90小时。考研无忧计划-尊享版<br>
          1.国内考试部开设的45人以上考研走读班和考研住宿班任选不限数量；<br>
          2.VIP一对一辅导90小时。考研无忧计划-升级版<br>
          1.可最多选报国内考试部开设的45人以上考研走读班型11<br>
          2.VIP一对一辅导50小时。考研无忧计划-精致版方案A：<br>
          可最多选报国内考试部开设的45人以上考研走读班型11个。方案B：1.可最多选报国内考试部开设的100人以上考研走读普通班型11个；2.VIP一对一辅导30小时。<br>
          考研无忧计划-协议通关版方案A：
          1.国内考试部开设的45人以上考研走读班和考研住宿班任选不限数量；<br>
          2.VIP一对一辅导100小时；<br>
          3.按照国家有关规定参加当次硕士研究生入学考试，符合双方约定的重读条件的，供乙方以下免费重读课程和服务，具体包含：第二年免费报名北京新东方学校国内考试部开设的100人以上考研走读普通班型任选不限数量及50小时考研VIP一对一课程。以上免费重读课程服务周期为一年；放弃享受免费重读的部分课程和服务不予退费；免费重读只可申请一次，若重读当年考研未通过，不可再次申请重读。
          方案B：<br>
          1.国内考试部开设的45人以上考研走读班和考研住宿班任选不限数量；<br>
          2.VIP一对一辅导150小时。<br>
        </p>
      </div>
  </div>
</template>

<script>
  import BScroll from 'better-scroll'
  import eventHub from 'assets/common/js/eventHub'

  export default {
    name: 'activity',
    data () {
      return {
        refreshed: false,
        startY: 0,
        endY: 0,
        showNavbar: true
      }
    },

    methods: {
      _initScroll () {
        // 初始化 BScroll，使用 ref 指定 DOM 元素
        this.contentScroll = new BScroll(this.$refs.contentWrapper, {
          probeType: 3,
          click: true
        })
        this.refreshed = false

        this.contentScroll.on('beforeScrollStart', () => {
          this.startY = this.endY
        })

        this.contentScroll.on('touchend', pos => {
          this.endY = Math.round(pos.y)
          this.showNavbar = (this.endY - this.startY) > 0
          eventHub.$emit('nav.show', this.showNavbar)
        })
      },
      refreshScrollOnce () {
        if (!this.refreshed) {
          this.contentScroll.refresh()
          this.refreshed = true
        }
      }
    },
    created () {
      this.$nextTick(() => {
        this._initScroll()
      })
    }
  }
</script>

<style lang="sass">
  #activity
    max-height: 100vh
    overflow: hidden
    background-color: white
    z-index: 1
    .content-wrapper
      min-height: 102vh
      .header
        position: relative
        .member
          display: flex
          position: fixed
          top: 0
          left: 0
          width: 100%
          height: 50px
          z-index: 10
          color: #fff
          justify-content: space-between
          background: #00a2e7
        .mu-icon
          font-sizeL 20px
        .name
          margin-top: 10px
          font-size: 18px
      .notice
        position: absolute
        top: 50px
        left: 0
        height: 50px
        width: 100%
        padding: 5px
        background-color: #f2f2f2
        img
          position: absolute
          left: 10px
          height: 40px
          float: left
        .label
          font-size: 14px
        .demo-raised-button
          position: absolute
          height: 30px
          width: 40px
          top: 10px
          right: 10px
          color: white
          background-color: #00a2e7
          border-radius: 5px
      .topic
        position: absolute
        top: 110px
        left: 10px
        text-align: left
        font-size: 20px
      .off
        position: absolute
        top: 180px
        width: 94%
        height: auto
        left: 3%
      .text
        position: absolute
        top: 410px
        padding: 10px
        text-align: left
        font-size: 5px
</style>



