<template>
  <div id="schedule">
    <div class="info">
      <div class="org">
        <p class="text">77</p>
        <p class="desc">已完成</p>
      </div>
      <div class="online">
        <p class="text">10</p>
        <p class="desc">未完成</p>
      </div>
      <div class="offline">
        <p class="text">10</p>
        <p class="desc">待开始</p>
      </div>
      <div class="rank">
        <p class="text">1
          <small>%</small>
        </p>
        <p class="desc">排名</p>
      </div>
    </div>
    <div class="schedule-list">
      <div class="schedule-item urgency">
        <mu-card>
          <mu-card-title title="完成CET6模拟试卷" subTitle="DEADLINE: 19/07/2017 20:00"/>
          <mu-card-text>
            内容和标题均可由用户自定义，加急，橙色
          </mu-card-text>
        </mu-card>
      </div>
      <div class="schedule-item minor">
        <mu-card>
          <mu-card-title title="约同学一起复习" subTitle="DEADLINE: 26/07/2017 20:00"/>
          <mu-card-text>
            同学还是存在的，次要
          </mu-card-text>
        </mu-card>
      </div>
      <div class="schedule-item fail">
        <mu-card>
          <mu-card-title title="绩点刷到5.0" subTitle="DEADLINE: 19/07/2016 20:00"/>
          <mu-card-text>
            失败的计划为灰色，成功为绿色
          </mu-card-text>
        </mu-card>
      </div>
      <div class="schedule-item success">
        <mu-card>
          <mu-card-title title="绩点刷到1.0" subTitle="DEADLINE: 19/07/2016 20:00"/>
          <mu-card-text>
            失败的计划为灰色，成功为绿色
          </mu-card-text>
        </mu-card>
      </div>
    </div>
  </div>
</template>

<script>
  export default {
    name: 'schedule'
  }
</script>

<style lang="sass">
  #schedule
    padding: 0 15px
    .info
      margin: 12px 0 20px 0
      display: flex
      width: 100%
      font-size: 14px
      > div
        width: calc(100% / 4)
        box-sizing: border-box
        .text
          font-size: 36px
          small
            font-size: 14px
        .desc
          color: #686868
        &:nth-of-type(n + 2)
          border-left: 2px solid #e5e5e5
    .schedule-list
      padding-bottom: 1px
      .schedule-item
        margin-bottom: 20px
        .mu-card
          padding: 10px
          text-align: left
          .mu-card-title-container
            padding-bottom: 0
        &.urgency
          .mu-card
            background: #FF8A65
        &.minor
          .mu-card
            background: #FFCC80
        &.fail
          .mu-card
            background: #BDBDBD
        &.success
          .mu-card
            background: #4DB6AC
</style>



