<template>
  <div id="header">
    <mu-icon-button href="#/profile" icon="person"></mu-icon-button>
    <mu-tabs :value="activeTab" @change="handleTabChange" class="tab">
      <mu-tab value="tab1" href="#/home" title="首页" class="tab-item"/>
      <mu-tab value="tab2" href="#/org" title="机构" class="tab-item"/>
      <mu-tab value="tab3" href="#/person" title="个人" class="tab-item"/>
    </mu-tabs>
    <mu-icon-button icon="search"/>
  </div>
</template>

<script>
  export default {
    name: 'header',
    data () {
      return {
        activeTab: 'tab1'
      }
    },
    methods: {
      handleTabChange (val) {
        this.activeTab = val
      }
    },
    created () {
      let url = this.$route.path
      this.activeTab = url.includes('home') ? 'tab1' : url.includes('org') ? 'tab2' : 'tab3'
    }
  }
</script>

<style lang="sass">
  #header
    display: flex
    box-shadow: 0 3px 5px rgba(0, 0, 0, 0.15)
    background-color: #00a1ea
    .tab
      display: flex
      margin: 0 auto
      background-color: rgba(0, 0, 0, 0)
      .tab-item
        flex: 1
</style>



