<template>
  <div id="index" ref="contentWrapper">
    <div class="content-wrapper" @touchstart="refreshScrollOnce">
      <mu-paper class="demo-paper" :zDepth="1">
        <div class="user-course">
          <div class="avatar-wrapper">
            <img src="../assets/images/avatar-1.png" width="58" height="58">
          </div>
          <div class="text-wrapper">
            <h2 class="title">3 courses today</h2>
            <p class="info">{{ getCurrentDate }} 南京</p>
          </div>
        </div>
      </mu-paper>
      <div class="guess-card">
        <mu-card>
          <mu-appbar title="猜你想学">
            <mu-icon-button icon="lightbulb_outline" slot="left"/>
          </mu-appbar>
        </mu-card>
      </div>
      <div class="guess">
        <div class="course-list">
          <mu-paper class="course-wrapper course-1 lg" :zDepth="1">
            <div class="title">CET4/6</div>
            <div class="desc">9.9元/时起</div>
          </mu-paper>
          <div class="courses">
            <mu-paper class="course-wrapper course-2 md" :zDepth="1">
              <div class="title">计算机</div>
              <div class="desc">C语言、C++、Java</div>
            </mu-paper>
            <mu-paper class="course-wrapper course-3 md" :zDepth="1">
              <div class="title">高数</div>
              <div class="desc">数竞大佬带你飞</div>
            </mu-paper>
            <mu-paper class="course-wrapper course-4 md" :zDepth="1">
              <div class="title">驾照</div>
              <div class="desc">老司机带你飞,报名</div>
            </mu-paper>
            <div class="courses">
              <mu-paper class="course-wrapper course-5 sm" :zDepth="1">
                <div class="title">托福</div>
                <div class="desc">20元/时</div>
              </mu-paper>
              <mu-paper class="course-wrapper course-6 sm" :zDepth="1">
                <mu-icon-button icon="more_horiz"/>
              </mu-paper>
            </div>
          </div>
        </div>
      </div>
      <div class="remind-card">
        <mu-card>
          <mu-appbar title="课程提醒">
            <mu-icon-button icon="alarm" slot="left"/>
          </mu-appbar>
          <mu-card-media title="围棋进阶课程" subTitle="7.14 11:20 | 南邮大活 101 房间">
            <img src="../assets/images/media-go.jpg"/>
          </mu-card-media>
        </mu-card>
      </div>
      <div class="new-card">
        <mu-card>
          <mu-appbar title="最近消息">
            <mu-icon-button icon="fiber_new" slot="left"/>
          </mu-appbar>
          <mu-card-title title="你预约的直播被取消" subTitle="2017.7.13 12:00"/>
          <mu-card-text>
            你在xx月xx日预约的直播已经被取消，款项已经通过 支付宝 退回您的账户。
          </mu-card-text>
        </mu-card>
      </div>
      <div class="nearby-card">
        <mu-card>
          <mu-appbar title="附近推荐">
            <mu-icon-button icon="location_on" slot="left"/>
          </mu-appbar>
          <mu-card-media>
            <img src="../assets/images/bg-map.png">
            <div class="content">
              <div class="text">
                <h2 class="title">附近有 23 人你可能感兴趣</h2>
                <p class="position">您当前在南京邮电大学</p>
              </div>
            </div>
          </mu-card-media>
        </mu-card>
      </div>
      <div class="follow-card">
        <mu-card>
          <mu-appbar title="你的关注">
            <mu-icon-button icon="face" slot="left"/>
          </mu-appbar>
          <div class="banner">
            <mu-card-media title="新东方夏季大促，立享五折">
              <img src="../assets/images/media-1.jpg">
            </mu-card-media>
            <mu-card-text>
              现在报名新东方立项超过五项优惠，最多可省100000元。
            </mu-card-text>
            <mu-divider/>
          </div>
          <div class="follow">
            <mu-card-text>
              <img class="thumb" src="../assets/images/logo-xdf.png">
              现在报名新东方立项超过五项优惠，最多可省100000元。
              <mu-icon-button>
                <i class="material-icons">keyboard_arrow_down</i>
              </mu-icon-button>
            </mu-card-text>
            <mu-divider/>
            <mu-card-text>
              <img class="thumb" src="../assets/images/logo-xdf.png">
              现在报名新东方立项超过五项优惠，最多可省100000元。
              <mu-icon-button>
                <i class="material-icons">keyboard_arrow_down</i>
              </mu-icon-button>
            </mu-card-text>
            <mu-divider/>
            <mu-card-text>
              <img class="thumb" src="../assets/images/logo-xdf.png">
              现在报名新东方立项超过五项优惠，最多可省100000元。
              <mu-icon-button>
                <i class="material-icons">keyboard_arrow_down</i>
              </mu-icon-button>
            </mu-card-text>
            <mu-raised-button label="More info" fullWidth/>
          </div>
        </mu-card>
      </div>
    </div>
  </div>
</template>

<script>
  import BScroll from 'better-scroll'
  import eventHub from 'assets/common/js/eventHub'
  import avatar1 from 'assets/images/avatar-1.png'
  import avatar2 from 'assets/images/avatar-2.png'

  export default {
    name: 'Index',
    data () {
      return {
        avatar1,
        avatar2,
        refreshed: false,
        startY: 0,
        endY: 0,
        showNavbar: true
      }
    },
    methods: {
      _initScroll () {
        // 初始化 BScroll，使用 ref 指定 DOM 元素
        this.contentScroll = new BScroll(this.$refs.contentWrapper, {
          probeType: 3,
          click: true
        })
        this.refreshed = false

        this.contentScroll.on('beforeScrollStart', () => {
          this.startY = this.endY
        })

        this.contentScroll.on('touchend', pos => {
          this.endY = Math.round(pos.y)
          this.showNavbar = (this.endY - this.startY) > 0
          eventHub.$emit('nav.show', this.showNavbar)
        })
      },
      refreshScrollOnce () {
        if (!this.refreshed) {
          this.contentScroll.refresh()
          this.refreshed = true
        }
      }
    },
    computed: {
      getCurrentDate () {
        let week = ['日', '一', '二', '三', '四', '五', '六']
        let myDate = new Date()
        return `${myDate.getFullYear()}.${myDate.getMonth() + 1}.${myDate.getDate()} 周${week[myDate.getDay()]}`
      }
    },
    created () {
      this.$nextTick(() => {
        this._initScroll()
      })
    },
    components: {}
  }
</script>

<style lang="sass">
  @import "../assets/common/sass/variables"
  @import "../assets/common/sass/mixins"

  #index
    max-height: calc(100vh - 48px)
    overflow-y: hidden
    .content-wrapper
      padding-bottom: 1px
      > [class $= '-card']
        @include baseCard
    .user-course
      display: flex
      padding: 0 18px
      height: 92px
      justify-content: space-between
      align-items: center
      .text-wrapper
        .title
          font-weight: normal
          font-size: 26px
        .info
          margin-top: -6px
          font-size: 16px
          color: #707070
    .guess
      margin-top: -14px
      padding: 0 18px
      @include baseTitle
      .course-list
        display: flex
        .courses
          display: flex
          flex-wrap: wrap
        .course-wrapper
          padding-top: 6px
          text-align: left
          border-radius: 3px
          .title
            margin-left: 10px
            font-size: 18px
            font-weight: bold
          .desc
            margin-top: -4px
            max-width: 100%
            max-height: 78px
            font-size: 12px
            text-overflow: ellipsis
          &.md
            width: $course-width
          &.sm
            width: $course-sm-width
          &.lg
            margin-top: 2px
            flex: 0 0 $course-width
            height: 114px
            background: url("../assets/images/bg-cet.png") no-repeat
            .desc
              margin-left: 12px
              font-weight: bold
          &.md, &.sm
            height: 54px
            .desc
              transform: scale(0.833)
          @include coursesMargin
          @include courseBackground
          &.course-6
            button
              width: 100%
          &.course-5
            .desc
              padding-left: 5px
    .new-card
      .mu-card-title
        font-size: 22px
      .mu-card-text
        padding-top: 0
    .nearby-card
      position: relative
      .mu-card-media
        img
          filter: brightness(0.6)
        .content
          display: flex
          position: absolute
          top: 0
          left: 0
          width: 100%
          height: 100%
          color: #fff
          text-align: center
          justify-content: center
          align-items: center
          flex-wrap: wrap
          border-radius: 6px
          .title
            font-size: 22px
            font-weight: 400
          .position
            font-size: 14px
            color: #e6e5e5
    .follow-card
      .banner
        margin-bottom: 10px
      .follow
        .mu-card-title-container
          padding-bottom: 0
        .mu-card-text
          position: relative
          .thumb
            float: left
            margin-right: 10px
          .mu-icon-button
            position: absolute
            right: 0
            bottom: 0
        .mu-raised-button-label
          color: rgba(0, 0, 0, 0.54)
</style>



