<template>
  <div id="center">
    <div class="tags">
      <mu-sub-header>兴趣标签</mu-sub-header>
      <mu-chip class="interest-chip">
        英语
      </mu-chip>
      <mu-chip class="interest-chip">
        计算机
      </mu-chip>
      <mu-chip class="interest-chip">
        设计
      </mu-chip>
      <mu-icon-button icon="add" class="add-interest"/>
    </div>
    <div class="portfolio">
      <mu-sub-header>学习历程</mu-sub-header>
      <div class="post-card">
        <mu-card>
          <mu-card-header :title="userName">
            <mu-avatar :src="avatar1" slot="avatar"/>
          </mu-card-header>
          <div class="post-time">1d</div>
          <mu-paper class="content-wrapper" :zDepth="1">
            <mu-card-media>
              <img src="../assets/images/media-2.jpg"/>
            </mu-card-media>
            <mu-card-title title="一米阳光"/>
            <mu-card-text>
              散落在指尖的阳光，我试着轻轻抓住光影的踪迹，它却在眉宇间投下一片淡淡的阴影。调皮的阳光掀动了四月的心帘，温暖如约的歌声渐起。
            </mu-card-text>
          </mu-paper>
          <mu-card-actions>
            <mu-icon-button icon="exposure_plus_1"/>
            <mu-icon-button icon="share"/>
            <mu-icon-button icon="comment"/>
          </mu-card-actions>
        </mu-card>
      </div>
      <div class="post-card">
        <mu-card>
          <mu-card-header :title="userName">
            <mu-avatar :src="avatar1" slot="avatar"/>
          </mu-card-header>
          <div class="post-time">1d</div>
          <mu-card-text>
            散落在指尖的阳光，我试着轻轻抓住光影的踪迹，它却在眉宇间投下一片淡淡的阴影。调皮的阳光掀动了四月的心帘，温暖如约的歌声渐起。
          </mu-card-text>
          <mu-card-actions>
            <mu-icon-button icon="exposure_plus_1"/>
            <mu-icon-button icon="share"/>
            <mu-icon-button icon="comment"/>
          </mu-card-actions>
        </mu-card>
      </div>
    </div>
  </div>
</template>

<script>
  import avatar1 from '../assets/images/avatar-1.png'

  export default {
    name: 'center',
    data () {
      return {
        avatar1,
        userName: 'Conway'
      }
    }
  }
</script>

<style lang="sass">
  @import "../assets/common/sass/variables"
  @import "../assets/common/sass/mixins"

  #center
    > [class $= '-card']
    .tags
      position: relative
      text-align: left
      .interest-chip
        margin-left: 5px
      .add-interest
        float: right
        right: 15px
        top: -8px
    .portfolio
      position: relative
      padding-bottom: 1px
      text-align: left
      @include postCard
</style>



