<template>
  <div id="profile" ref="contentWrapper">
    <div class="content-wrapper" @touchstart="refreshScroll">
      <div class="header">
        <div class="menubar">
          <mu-icon-button icon="arrow_back" href="/"/>
          <div class="name">
            Conway
          </div>
          <mu-icon-button icon="more_vert"/>
        </div>
        <mu-card>
          <mu-card-media>
            <img src="../assets/images/media-3.jpg">
          </mu-card-media>
          <div class="info">
            <mu-avatar :src="avatar1" :size="70"/>
            <mu-chip class="check-chip">已认证</mu-chip>
          </div>
        </mu-card>
        <mu-tabs :value="activeTab" @change="handleTabChange" class="tab">
          <mu-tab value="tab1" href="#/profile" title="个人中心" class="tab-item"/>
          <mu-tab value="tab2" href="#/profile/track" title="学习记录" class="tab-item"/>
          <mu-tab value="tab3" href="#/profile/schedule" title="计划本" class="tab-item"/>
        </mu-tabs>

        <keep-alive>
          <router-view></router-view>
        </keep-alive>
      </div>
    </div>
  </div>
</template>

<script>
  import BScroll from 'better-scroll'
  import eventHub from 'assets/common/js/eventHub'
  import avatar1 from 'assets/images/avatar-1.png'

  export default {
    name: 'profile',
    data () {
      return {
        activeTab: 'tab1',
        avatar1
      }
    },
    methods: {
      handleTabChange (val) {
        this.activeTab = val
      },
      _initScroll () {
        // 初始化 BScroll，使用 ref 指定 DOM 元素
        this.contentScroll = new BScroll(this.$refs.contentWrapper, {
          probeType: 3,
          click: true
        })
        this.refreshed = false

        this.contentScroll.on('beforeScrollStart', () => {
          this.startY = this.endY
        })

        this.contentScroll.on('touchend', pos => {
          this.endY = Math.round(pos.y)
          this.showNavbar = (this.endY - this.startY) > 0
          eventHub.$emit('nav.show', this.showNavbar)
        })
      },
      refreshScroll () {
        this.contentScroll.refresh()
      }
    },
    created () {
      let url = this.$route.path
      this.activeTab = url.includes('track') ? 'tab2' : url.includes('schedule') ? 'tab3' : 'tab1'

      this.$nextTick(() => {
        this._initScroll()
      })
    }
  }
</script>

<style lang="sass">
  #profile
    max-height: 100vh
    overflow: hidden
    z-index: 1
    > .content-wrapper
      min-height: 102vh
    .header
      position: relative
      .menubar
        display: flex
        position: fixed
        top: 0
        left: 0
        width: 100%
        height: 50px
        z-index: 10
        color: #fff
        justify-content: space-between
        background: linear-gradient(0deg, rgba(200, 200, 200, 0), rgba(7, 17, 27, 0.45))
        .mu-icon
          font-size: 20px
        .name
          margin-top: 10px
          font-size: 18px
      > .mu-card
        position: relative
        .mu-card-media
          margin-left: -4px
          width: 102%
          height: 211px
          filter: brightness(0.6)
          img
            width: 100%
            height: 100%
            filter: blur(4px)
        .info
          position: absolute
          top: 0
          width: 100%
          height: 100%
          padding-top: 10vh
        .mu-avatar
          display: block
          margin: 0 auto 20px auto
          border: 1px solid #fff
        .mu-card-title-container
          margin-top: -17px
          .mu-card-title
            color: #fff
      .tab
        margin-top: -4px
        box-shadow: 0 1px 6px rgba(0, 0, 0, .117647), 0 1px 4px rgba(0, 0, 0, .117647)
        background: #fff
        .mu-tab-text
          color: #000
</style>



