<template>
  <div id="organization" ref="contentWrapper">
    <div class="content-wrapper" @touchstart="refreshScrollOnce">
      <div class="container">
        <span class="slider ">
          <img src="../assets/images/media-2.jpg">
        </span>
      </div>
      <span class="topic">大家在找</span>
      <mu-flexbox class="find">
        <mu-flexbox-item class="find-item">四六级</mu-flexbox-item>
        <mu-flexbox-item class="find-item">雅思／托福</mu-flexbox-item>
        <mu-flexbox-item class="find-item">计算机二级</mu-flexbox-item>
        <mu-flexbox-item class="find-item">初升高衔接</mu-flexbox-item>
      </mu-flexbox>
      <mu-flexbox class="find">
        <mu-flexbox-item class="find-item">高中物理</mu-flexbox-item>
        <mu-flexbox-item class="find-item">中考英语</mu-flexbox-item>
        <mu-flexbox-item class="find-item">驾照</mu-flexbox-item>
        <mu-flexbox-item class="find-item">更多</mu-flexbox-item>
      </mu-flexbox>
      <span class="topic">全部机构</span>
      <div class="agency">
        <a href="#/org/1">
          <mu-card class="ogz card-1">
            <img src="../assets/images/icon-newEath.png">
            <button title="订阅" class="get">+订阅</button>
            <span class="Category">CET | 雅思 | 托福 | GRE</span><br>
            <span class="into">点击进入机构页面</span>
          </mu-card>
        </a>
        <mu-card class="ogz card-2">
          <img src="../assets/images/icon-learnandthink.png">
          <button title="订阅" class="get">+订阅</button>
          <span class="Category">CET | 雅思 | 托福 | GRE</span><br>
          <span class="into">点击进入机构页面</span>
        </mu-card>
      </div>
    </div>
  </div>
</template>

<script>
  import BScroll from 'better-scroll'
  import eventHub from 'assets/common/js/eventHub'

  export default {
    name: 'organization',
    data () {
      return {
        refreshed: false,
        startY: 0,
        endY: 0,
        showNavbar: true
      }
    },
    methods: {
      _initScroll () {
        // 初始化 BScroll，使用 ref 指定 DOM 元素
        this.contentScroll = new BScroll(this.$refs.contentWrapper, {
          probeType: 3,
          click: true
        })
        this.refreshed = false

        this.contentScroll.on('beforeScrollStart', () => {
          this.startY = this.endY
        })

        this.contentScroll.on('touchend', pos => {
          this.endY = Math.round(pos.y)
          this.showNavbar = (this.endY - this.startY) > 0
          eventHub.$emit('nav.show', this.showNavbar)
        })
      },
      refreshScrollOnce () {
        if (!this.refreshed) {
          this.contentScroll.refresh()
          this.refreshed = true
        }
      }
    },
    created () {
      this.$nextTick(() => {
        this._initScroll()
      })
    }
  }
</script>

<style lang="sass">
  @import "../assets/common/sass/variables"
  @import "../assets/common/sass/mixins"

  #organization
    max-height: calc(100vh - 48px)
    overflow-y: hidden
    background-color: white
    .content-wrapper
      min-height: 102vh
      .container
        height: 200px
        .slider
          height: 200px
          width: 100%
          img
            height: 200px
            width: 100%
            float: left
      .find
        padding: 5px 10px 0 10px
      .find-item
        text-align: center
        line-height: 40px
        height: 40px
        background-color: #f4f4f4
        border: 1px solid white
        border-radius: 5px
      .topic
        font-size: 18px
        float: left
        font-weight: bold
        margin-left: 12px
      .agency
        margin-top: 25px
        padding: 10px 10px 0 10px
        .ogz
          height: 120px
          margin-bottom: 7px
          background-color: #0b685f
          box-shadow: 0 0 20px grey
          border-radius: 10px
          .get
            margin-top: 15px
            margin-bottom: 10px
            margin-left: 90px
            color: white
            font-size: 18px
            font-weight: bold
        .card-2
          background-color: #9b3cfd
        img
          float: left
          height: 60px
          width: 160px
          margin-top: 30px
          margin-left: 20px
        .Category
          margin-top: 60px
          margin-right: -10px
          font-size: 13px
          color: white
        .into
          color: #ccc
          margin-right: -30px
</style>



