<script>
  import BScroll from 'better-scroll'
  import eventHub from 'assets/common/js/eventHub'
  import image1 from 'assets/images/newsession.png'

  export default {
    name: 'example',
    data () {
      return {
        activeTab: 'tab1',
        list: [{
          image: image1,
          title: '开学辅导班优惠',
          time: '2017/7/13',
          featured: true
        }, {
          image: image1,
          title: 'Burger',
          ime: '2017/7/13'
        }, {
          image: image1,
          title: 'Camera',
          time: '2017/7/13'
        }]
      }
    },

    methods: {
      handleTabChange (val) {
        this.activeTab = val
      },
      _initScroll () {
        // 初始化 BScroll，使用 ref 指定 DOM 元素
        this.contentScroll = new BScroll(this.$refs.contentWrapper, {
          probeType: 3,
          click: true
        })
        this.refreshed = false

        this.contentScroll.on('beforeScrollStart', () => {
          this.startY = this.endY
        })

        this.contentScroll.on('touchend', pos => {
          this.endY = Math.round(pos.y)
          this.showNavbar = (this.endY - this.startY) > 0
          eventHub.$emit('nav.show', this.showNavbar)
        })
      },
      refreshScroll () {
        this.contentScroll.refresh()
      }
    },

    created () {
      let url = this.$route.path
      this.activeTab = url.includes('track') ? 'tab2' : url.includes('schedule') ? 'tab3' : url.includes('track') ? 'tab4' : url.includes('track') ? 'tab5' : 'tab1'

      this.$nextTick(() => {
        this._initScroll()
      })
    }
  }
</script>

