            margin-top: 10px
            height: 30px
            width: auto
          p
            font-size: 10px
            margin-top: -10px
          .someone
            position: relative
            top: -18px
            margin-left: 5px
            font-size: 14px
            font-weight: bold
          .time
            position: relative
            top: -18px
            margin-left: 5px
            font-size: 3px
          .paid
            position: relative
            top: -19px
            margin-left: 39px
