<template>
  <div id="Layout">
    <!-- header -->
    <keep-alive>
      <my-header></my-header>
    </keep-alive>

    <!-- main -->
    <router-view class="main"></router-view>

    <mu-float-button icon="center_focus_strong" class="float-button"/>
    <!-- footer -->
    <keep-alive>
      <my-footer></my-footer>
    </keep-alive>
  </div>
</template>

<script>
  import myHeader from 'components/Header'
  import myFooter from 'components/Footer'

  export default {
    name: 'layout',
    data () {
      return {
      }
    },
    components: {
      myHeader,
      myFooter
    }
  }
</script>

<style lang="sass">
  #layout
</style>



