  <div id="person" ref="contentWrapper">
    <div class="content-wrapper" @touchstart="refreshScrollOnce">
      <mu-flexbox class="Classification">
        <mu-flexbox-item class="class-item">
          <a href="#/person/class"><img src="../assets/images/logo-teacher.png" class="item-img"></a>
        </mu-flexbox-item>
        <mu-flexbox-item class="class-item">
          <img src="../assets/images/logo-ability.png" class="item-img">
        </mu-flexbox-item>
        <mu-flexbox-item class="class-item">
          <img src="../assets/images/logo-sports.png" class="item-img">
        </mu-flexbox-item>
        <mu-flexbox-item class="class-item">
          <img src="../assets/images/logo-love.png" class="item-img">
        </mu-flexbox-item>
      </mu-flexbox>
      <mu-flexbox class="Classification-des">
        <mu-flexbox-item class="class-item">家教</mu-flexbox-item>
        <mu-flexbox-item class="class-item">技能</mu-flexbox-item>
        <mu-flexbox-item class="class-item">体育</mu-flexbox-item>
        <mu-flexbox-item class="class-item">兴趣</mu-flexbox-item>
      </mu-flexbox>
      <span class="topic">猜你喜欢</span><br>
      <div class="interest">
        <div class="interest-list">
          <div class="interest-wrapper interest-1 lg">
            <div class="title">计算机理论基础</div>
            <div class="desc">陈立杰</div>
          </div>
          <div class="interests">
            <div class="interest-wrapper interest-2 md">
              <div class="title">C语言入门</div>
              <div class="desc">某某某</div>
            </div>
            <div class="interest-wrapper interest-3 md">
              <div class="title">演讲与口才</div>
              <div class="desc">某某某</div>
            </div>
            <div class="interest-wrapper interest-4 md">
              <div class="title">王者荣耀</div>
              <div class="desc">某某某</div>
            </div>
            <div class="interest-wrapper interest-5 md">
              <div class="title">狼人杀</div>
              <div class="desc">某某某</div>
            </div>
          </div>
        </div>
      </div>
      <span class="topic">热门推荐</span>
      <div class="hot">
        <div class="hot-item-list">
          <mu-card class="hot-item">
            <mu-card-title title="围棋的进阶 - 中国围棋史" subTitle="柯洁 - 围棋世界冠军"/>
            <img src="../assets/images/avatar3.png" class="kejie">
            <button class="tag">围棋</button>
            <span class="star">
                <img src="../assets/images/ic_star_3x.png">
                <img src="../assets/images/ic_star_3x.png">
                <img src="../assets/images/ic_star_3x.png">
                <img src="../assets/images/ic_star_3x.png">
                <img src="../assets/images/ic_star_3x.png">
              </span>
          </mu-card>
          <mu-card class="hot-item">
            <mu-card-title title="围棋的进阶 - 中国围棋史" subTitle="柯洁 - 围棋世界冠军"/>
            <button class="tag">围棋</button>
            <img src="../assets/images/avatar3.png" class="kejie">
            <span class="star">
                <img src="../assets/images/ic_star_3x.png">
                <img src="../assets/images/ic_star_3x.png">
                <img src="../assets/images/ic_star_3x.png">
                <img src="../assets/images/ic_star_3x.png">
                <img src="../assets/images/ic_star_3x.png">
              </span>
          </mu-card>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  import BScroll from 'better-scroll'
  import eventHub from 'assets/common/js/eventHub'

  export default {
    name: 'person',
    data () {
      return {
        refreshed: false,
        startY: 0,
        endY: 0,
        showNavbar: true
      }
    },
    methods: {
      _initScroll () {
        // 初始化 BScroll，使用 ref 指定 DOM 元素
        this.contentScroll = new BScroll(this.$refs.contentWrapper, {
          probeType: 3,
          click: true
        })
        this.refreshed = false

        this.contentScroll.on('beforeScrollStart', () => {
          this.startY = this.endY
        })

        this.contentScroll.on('touchend', pos => {
          this.endY = Math.round(pos.y)
          let deltaY = this.endY - this.startY
          if (deltaY === 0) {
            return
          }
          this.showNavbar = (deltaY) > 0
          eventHub.$emit('nav.show', this.showNavbar)
        })
      },
      refreshScrollOnce () {
        if (!this.refreshed) {
          this.contentScroll.refresh()
          this.refreshed = true
        }
      }
    },
    created () {
      this.$nextTick(() => {
        this._initScroll()
      })
    }
  }
</script>

<style lang="sass">
  @import "../assets/common/sass/variables"
  @import "../assets/common/sass/mixins"

  #person
    max-height: calc(100vh - 48px)
    min-height: 101vh
    overflow-y: hidden
    .content-wrapper
      min-height: 102vh
    .Classification
      height: 90px
      background-color: #faf9f9
    .Classification-des
      height: 20px
      margin-top: -20px
    .class-item
      text-align: center
      line-height: 80px
    .item-img
      width: 55px
      height: auto
      margin-top: 20px
    .topic
      font-size: 18px
      float: left
      margin-top: 10px
      margin-left: 18px
      font-weight: bold
    .interest
      margin-top: 30px
      padding: 0 15px
      @include baseTitle
      .interest-list
        display: flex
        .interests
          display: flex
          flex-wrap: wrap
        .interest-wrapper
          padding-top: 6px
          text-align: left
          .title
            margin-left: 10px
            font-size: 14px
            font-weight: bold
            color: white
          .desc
            margin-top: -4px
            max-width: 100%
            max-height: 78px
            font-size: 13px
            text-overflow: ellipsis
          &.md
            flex: 0 0 $interest-md-width
            margin-left: 2%
            background: url("../assets/images/Jack.png")
            background-size: 100%
            background-repeat: no-repeat
            width: 48%
            height: 80px
            .title
              margin-top: 40px
          &.lg
            flex: 0 0 $interest-width
            height: 170px
            margin-top: -2px
            background: url("../assets/images/Daive.png")
            background-size: 100%
            background-repeat: no-repeat
            width: 100%
            /*box-shadow: 0 -10px 10px black*/
            .title
              font-size: 16px
              margin-top: 115px
          .desc
            color: white
            font-weight: bold
          .desc
            transform: scale(0.833)
          @include coursesMargin
          @include courseBackground
    .hot
      margin-top: 30px
      width: 100%
      padding: 18px
      .hot-item
        margin-bottom: 15px
        height: 100px
    .mu-card-title-container
      float: left
      padding: 10px 0 5px 10px
      .mu-card-title
        font-size: 18px
    .mu-card-sub-title
      float: left
      font-size: 14px
    .tag
      position: absolute
      height: 25px
      width: 45px
      /*line-height: 25px*/
      left: 10px
      top: 70px
      border: 1px solid grey
      border-radius: 10px
      background-color: white
      color: grey
    .kejie
      float: right
      height: 80px
      margin-top: 8px
      margin-right: 10px
    .star
      position: absolute
      left: 50px
      top: 73px
      width: 150px
      height: 20px
      flex-direction: row
      img
        height: 18px

</style>




