<template>
  <div id="track">
    <div class="info">
    <div class="org">
      <p class="text">9</p>
      <p class="desc">机构排名</p>
    </div>
    <div class="online">
      <p class="text">12</p>
      <p class="desc">在线学习</p>
    </div>
    <div class="offline">
      <p class="text">5</p>
      <p class="desc">线下约学</p>
    </div>
    <div class="rank">
      <p class="text">10<small>%</small>
      </p>
      <p class="desc">排名</p>
    </div>
  </div>
    <div class="track-list">
      <div class="track-item">
        <mu-card>
          <div class="header">
            <div class="icon">
              <img src="../assets/images/icon-xdf.png" width="42" height="42">
            </div>
            <h2 class="title">
              新东方暑假进修课程 <span class="stat-unfinished">[未开始]</span>
            </h2>
          </div>
          <div class="track-info">
            <div class="track-time">
              <small class="study-time">学习5小时</small>
              <small class="time">2017.8.10-2017.9.10</small>
            </div>
            <div class="self-evaluation">
              <small class="desc">自我评价</small>
              <small>课程未开始</small>
            </div>
          </div>
        </mu-card>
      </div>
      <div class="track-item">
        <mu-card>
          <div class="header">
            <div class="icon">
              <img src="../assets/images/icon-who.png" width="42" height="42">
            </div>
            <h2 class="title">
              新东方暑假进修课程 <span class="stat-finished">[结束]</span>
            </h2>
          </div>
          <div class="track-info">
            <div class="track-time">
              <small class="study-time">学习4小时</small>
              <small class="time">2017.8.10-2017.9.10</small>
            </div>
            <div class="self-evaluation">
              <small class="desc">自我评价</small>
              <small class="stars">
                <ul class="stars">
                  <li v-for="item in stars" class="star-item">
                    <mu-icon :value="item" color="#ffc100" :size="18"/>
                  </li>
                </ul>
              </small>
            </div>
          </div>
        </mu-card>
      </div>
      <div class="track-item">
        <mu-card>
          <div class="header">
            <div class="icon">
              <img src="../assets/images/icon-xdf.png" width="42" height="42">
            </div>
            <h2 class="title">
              新东方暑假进修课程 <span class="stat-finished">[结束]</span>
            </h2>
          </div>
          <div class="track-info">
            <div class="track-time">
              <small class="study-time">学习8小时</small>
              <small class="time">2017.8.10-2017.9.10</small>
            </div>
            <div class="self-evaluation">
              <small class="desc">自我评价</small>
              <small class="stars">
                <ul class="stars">
                  <li v-for="item in stars" class="star-item">
                    <mu-icon :value="item" color="#ffc100" :size="18"/>
                  </li>
                </ul>
              </small>
            </div>
          </div>
        </mu-card>
      </div>
    </div>
  </div>
</template>

<script>
  const LENGTH = 5
  const STAR_ON = 'star'
  const STAR_HALF = 'star_half'
  const STAR_OFF = 'star_border'

  export default {
    name: 'track',
    data () {
      return {
        score: 3.6
      }
    },
    computed: {
      stars () {
        let result = []
        let score = Math.floor(this.score * 2) / 2  // 向下取0.5
        let hasDecimal = score % 1 !== 0  // 判断是否有半星
        let integer = Math.floor(score)   // 判断有几个全星
        for (let i = 0; i < integer; i++) {
          result.push(STAR_ON)
        }
        if (hasDecimal) {
          result.push(STAR_HALF)
        }
        while (result.length < LENGTH) {
          result.push(STAR_OFF)
        }
        return result
      }
    }
  }
</script>

<style lang="sass">
  #track
    padding: 0 15px
    .info
      margin: 12px 0 20px 0
      display: flex
      width: 100%
      font-size: 14px
      > div
        width: calc(100% / 4)
        box-sizing: border-box
        .text
          font-size: 36px
          small
            font-size: 14px
        .desc
          color: #686868
        &:nth-of-type(n + 2)
          border-left: 2px solid #e5e5e5
    .track-list
      padding-bottom: 1px
      .track-item
        margin-bottom: 20px
        .mu-card
          padding: 10px
        .header
          .title
            padding-top: 8px
            font-size: 18px
            text-indent: 0.7em
            text-align: left
          .stat-finished
            color: #818181
          .stat-unfinished
            color: #027c14
        .icon
          float: left
        .mu-card-title
          font-size: 20px
          font-weight: 500
        .track-info
          margin: 4px 0 0 55px
          text-align: left
          .self-evaluation
            .desc
              display: inline-block
            .stars
              display: inline-block
              vertical-align: -0.4em
              ul
                padding: 0
              li
                float: left
                list-style-type: none
</style>



