<template>
  <div id="class" ref="contentWrapper">
    <div class="content-wrapper" @touchstart="refreshScroll">
      <div class="header">
        <div class="member">
          <mu-icon-button icon="arrow_back" href="#/person"/>
          <div class="name">
            个人
          </div>
          <mu-icon-button icon="more_vert"/>
        </div>
      </div>
      <mu-flexbox class="Classification">
        <mu-flexbox-item class="class-item">
          <img src="../assets/images/logo-teacher.png" class="item-img"><span class="item-des">家教</span>
        </mu-flexbox-item>
        <mu-flexbox-item class="class-item">
          <img src="../assets/images/logo-ability.png" class="item-img"><span class="item-des">技能</span>
        </mu-flexbox-item>
        <mu-flexbox-item class="class-item">
          <img src="../assets/images/logo-sports.png" class="item-img"><span class="item-des">体育</span>
        </mu-flexbox-item>
        <mu-flexbox-item class="class-item">
          <img src="../assets/images/logo-love.png" class="item-img"><span class="item-des">兴趣</span>
        </mu-flexbox-item>
      </mu-flexbox>
      <div class="complete">
        <img src="../assets/images/icon-search.png">
        <mu-auto-complete hintText="请随便输入点啥" @input="handleInput" :dataSource="dataSource" @change="handlechange" class="input"/>
      </div>
      <span class="libary">高中数学&nbsp;&nbsp;&nbsp;C语言&nbsp;&nbsp;&nbsp;C++&nbsp;&nbsp;&nbsp;野外生存&nbsp;&nbsp;&nbsp;定向越野</span>
      <i class="material-icons icon">expand_more</i>
      <div class="card-list">
        <mu-card class="card-1">
          <mu-card-media title="专业钢琴家教" subTitle="25元／时起" titleClass="title" subTitleClass="sub">
            <img src="../assets/images/langlang.png" />
          </mu-card-media>
        </mu-card>
        <mu-card class="card-2">
          <mu-card-media title="专业钢琴家教" subTitle="25元／时起" titleClass="title" subTitleClass="sub">
            <img src="../assets/images/teacher.png" />
          </mu-card-media>
        </mu-card>
        <mu-card class="card-3">
          <mu-card-media title="专业钢琴家教" subTitle="25元／时起" titleClass="title" subTitleClass="sub">
            <img src="../assets/images/teacher.png" />
          </mu-card-media>
        </mu-card>
        <mu-card class="card-1">
          <mu-card-media title="专业钢琴家教" subTitle="25元／时起" titleClass="title" subTitleClass="sub">
            <img src="../assets/images/langlang.png" />
          </mu-card-media>
        </mu-card>
      </div>
    </div>
  </div>
</template>

<script>
  import BScroll from 'better-scroll'
  import eventHub from 'assets/common/js/eventHub'

  export default {
    name: 'class',
    data () {
      return {
        dataSource: []
      }
    },
    methods: {
      handlechange (val) {
        console.log(`you choose ${val}`)
      },

      handleInput (val) {
        this.dataSource = [
          val,
          val + '课程',
          val + '家教',
          val + '兼职'
        ]
      },

      _initScroll () {
        // 初始化 BScroll，使用 ref 指定 DOM 元素
        this.contentScroll = new BScroll(this.$refs.contentWrapper, {
          probeType: 3,
          click: true
        })
        this.refreshed = false

        this.contentScroll.on('beforeScrollStart', () => {
          this.startY = this.endY
        })

        this.contentScroll.on('touchend', pos => {
          this.endY = Math.round(pos.y)
          this.showNavbar = (this.endY - this.startY) > 0
          eventHub.$emit('nav.show', this.showNavbar)
        })
      },
      refreshScroll () {
        this.contentScroll.refresh()
      },

      created () {
        this.$nextTick(() => {
          this._initScroll()
        })
      }
    }
  }
</script>

<style lang="sass">
  #class
    max-height: 10vh
    overflow: hidden
    background-color: white
    z-index: 1
    .content-wrapper
      min-height: 102vh
      .header
        position: relative
        .member
          display: flex
          position: fixed
          top: 0
          left: 0
          width: 100%
          height: 80px
          z-index: 10
          color: #fff
          justify-content: space-between
          background: linear-gradient(0deg, rgba(200, 200, 200, 0), rgba(7, 17, 27, 0.45))
          .mu-icon
            font-sizeL 20px
          .name
            margin-top: 10px
            font-size: 18px
      .Classification
        position: absolute
        top: 75px
        height: 40px
        line-height: 40px
        margin-left: 10px
        .class-item
          height: 40px
          width: auto
          img
            width: 35px
            height: auto
          .item-des
            position: absolute
            top: -3px
            margin-left: 5px
      .complete
        position: absolute
        top: 130px
        left: 2%
        height: 40px
        width: 96%
        border: 1px solid #00a2e7
        border-radius: 5px
        line-height: 40px
        .mu-text-field-input
          width: 100%
        img
          height: 30px
          float: left
          margin-top: 5px
          margin-left: 10px
      .libary
        position: absolute
        top: 180px
        left: 10px
      .card-list
        position: absolute
        top: 210px
        padding: 5px
        float: left
        .card-1
          width: 47%
          height: 200px
          margin: 5px
          float: left
          border-radius: 5px
          box-shadow: 0 10px 50px 0 grey
          font-size: 18px
        .card-2
          width: 47%
          height: 240px
          margin: 5px
          float: left
          border-radius: 5px
          box-shadow: 0 0 5px 0.1px grey
          img
            height: 240px
        .card-3
          width: 47%
          height: 240px
          margin: -15px 5px 5px 5px
          float: left
          border-radius: 5px
          box-shadow: 0 10px 50px 0 grey
          img
            height: 240px
        .title
          font-size: 19px
          font-weight: bold
          text-align: left
        .sub
          font-size: 10px
          text-align: left
      .icon
          position: absolute
          top: 180px
          right: 10px
          float: right

</style>




