<template>
  <transition name="slide">
    <div id="footer" v-show="show">
      <mu-paper>
        <mu-bottom-nav :value="bottomNav" shift @change="handleChange">
          <mu-bottom-nav-item value="nearby" title="附近" icon="location_on"/>
          <mu-bottom-nav-item value="message" title="消息" icon="message"/>
          <mu-bottom-nav-item value="course" title="课程" icon="library_books"/>
          <mu-bottom-nav-item value="live" title="直播" icon="live_tv"/>
        </mu-bottom-nav>
      </mu-paper>
    </div>
  </transition>
</template>

<script>
  import eventHub from 'assets/common/js/eventHub'

  export default {
    name: 'footer',
    data () {
      return {
        bottomNav: 'nearby',
        bottomNavColor: 'nearby',
        show: true
      }
    },
    methods: {
      handleChange (val) {
        this.bottomNav = val
      }
    },
    created () {
      eventHub.$on('nav.show', data => {
        this.show = data
      })
    }
  }
</script>

<style lang="sass">
  #footer
    position: fixed
    bottom: 0
    left: 0
    z-index: 10
    width: 100%
    .mu-bottom-nav-shift
      background-color: #f9f9f9
      *
        color: #6c6c6c
      .mu-bottom-item-text
        margin-top: -5px
  .slide-enter-active, .slide-leave-active
    transition: transform .2s
  .slide-enter, .slide-leave-to
    transform: translate3d(0, 54px, 0)
</style>



